.modal{
    width: 32%;
    height: 250px;
    background-color: rgb(202, 68, 68);
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    color: #fff;
    font-weight: bold;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -100%);
    z-index: 0;
    &__overlay{
        position: fixed;
        width: 100vw;
        height: 100vh;
        background-color: rgba(153, 153, 153, 0.5);
        z-index: 1;
        transform: scale(0);
        &.active{
            transform: scale(0);
        }
    }
    &__header{
        background-color: rgb(175, 59, 59);
        color: #fff;
        font-weight: bold;
        font-size: 20px;
        border-radius: 7px 7px 0 0;
        flex-grow: 1;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 30px;
        &__closeBtn{
            width: 30px;
            height: 30px;
            position: relative;
            background-color: transparent;
            &::before{
                content: "";
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                height: 30px;
                width: 2px;
                transform: rotate(45deg);
                background-color: rgb(255, 255, 255);
            }
            &::after{
                content: "";
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                height: 30px;
                width: 2px;
                border-radius: 20%;
                transform: rotate(-45deg);
                background-color: rgb(255, 255, 255);
            }
        }
    }
    &__body{
        flex-grow: 4;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 20px 40px;
        line-height: 30px;
        font-size: 16px;
    }
    &__footer{
        border-radius: 0 0 7px 7px;
        flex-grow: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px;
        &__successBtn{
            font-weight: bold;
            color: #fff;
            background-color: rgb(161, 32, 32);
            width: 25%;
            height: 45px;
            margin-right: 8px;
            border-radius: 5px;
        }
        &__closeBtn{
            font-weight: bold;
            color: #fff;
            background-color: rgb(161, 32, 32);
            width: 25%;
            height: 45px;
            margin-left: 8px;
            border-radius: 5px;
        }
    }

}